import React from "react";
import Modal from "@material-ui/core/Modal";

function Subscribe({ open }) {
  return (
    <Modal open={open}>
      <iframe
        src="https://docs.google.com/forms/d/e/1FAIpQLSesulxRiLNJ7u1jhNiaD4VhD6dvnuo91r3dYH6GAOkaEG_GJA/viewform?embedded=true"
        width="640"
        height="536"
        frameborder="0"
        marginheight="0"
        marginwidth="0"
      >
        Loading...
      </iframe>
    </Modal>
  );
}

export default Subscribe;
